.draggable-handle {
  position: absolute;
  color: #aaa;
  top: 0;
  left: 0;
  height: 100%;
  background: #eee;
  font-size: 150%;
  display: flex;
  align-items: center;
  cursor: move;
  line-height: normal;
}

.draggable {
  padding-left: 1.75rem;
}

.list-group-add {
  padding-right: 1.25rem;
  border-width: 1px 1px 1px 0;
  border-style: solid;
  border-color: transparent;
}

.list-placeholder {
  color: #6c757d;
  padding-top: initial;
  padding-bottom: initial;
}
